function Works() {
    const languaje = useSelector((state) => state.languaje)

    return (
        <Box>
            <Box
                sx={{
                    display: 'flex',
                    paddingLeft: '3.5rem',
                    paddingRight: '3.5rem',
                    paddingTop: '3rem',
                    paddingBottom: '3rem',
                }}
            >
                <Typography variant="h2" fontSize="2.5rem" fontWeight="700">
                    Portfolio
                </Typography>
                <Box
                    sx={{
                        width: '12rem',
                        height: '0.125rem',
                        borderRadius: '0.375rem',
                        backgroundColor: 'secondary.main',
                        left: '2rem',
                        top: '1.5rem',
                        position: 'relative',
                        display: { xs: 'none', md: 'block' },
                    }}
                />
            </Box>
            <Grid container>
                {Proyects.map((proyect) => {
                    const ProyectImg = proyect.img
                    return (
                        <Grid xs={6} md={4} sx={{ paddingLeft: '0px' }}>
                            <Box
                                sx={{
                                    display: 'flex',
                                    flexDirection: 'column',
                                    justifyContent: 'center',
                                    backgroundColor: 'primary.main',
                                    marginLeft: '1.25rem',
                                    marginRight: {
                                        xs: '1.25rem',
                                        md: '0.625rem',
                                    },
                                    marginBottom: '1.5rem',
                                    borderRadius: '0.5rem',
                                    paddingLeft: '1.25rem',
                                    paddingRight: '0.75rem',
                                    paddingTop: '1rem',
                                    paddingBottom: '1rem',
                                }}
                            >
                                <img
                                          src={`${proyect.img}`}
                                          alt={proyect.description}
                                      />
                                <Typography
                                    variant="h4"
                                    sx={{
                                        fontSize: '.875rem',
                                        opacity: '0.85',
                                    }}
                                >
                                    {
                                            proyect.description.filter(
                                                (text) => text.lang === languaje
                                            )[0].description
                                        }
                                </Typography>
                                <Typography
                                    variant="h3"
                                    sx={{
                                        fontSize: '0.85rem',
                                        marginTop: '0.5rem',
                                    }}
                                >
                                    asdf
                                </Typography>
                            </Box>
                        </Grid>
                    )
                })}
            </Grid>
            <Box
                sx={{
                    display: 'flex',
                    flexWrap: 'wrap',
                    marginLeft: '1.25rem',
                    rowGap: '0.75rem',
                    columnGap: '0.75rem',
                }}
            >
                {console.log(technologies)}
                {technologies.map((element) => {
                    return (
                        <Chip
                            key={element.id}
                            label={
                                element.description.filter(
                                    (text) => text.lang === languaje
                                )[0].description
                            }
                        />
                    )
                })}
            </Box>
        </Box>
    )
}

export default Works
